<template>
  <Card :bordered="false" dis-hover>
    <Form :model="queryForm" ref="searchDiv" :label-width="80">
      <!-- <Row :gutter="16">
        <Col span="6">
          <FormItem label="应用ID:" prop="id">
            <Input v-model="queryForm.id" placeholder="请输入"></Input>
          </FormItem>
        </Col>

        <Col span="6">
          <FormItem label="应用Code:" prop="code">
            <Input v-model="queryForm.code" placeholder="请输入"></Input>
          </FormItem>
        </Col>
        <Col span="6">
          <FormItem label="应用编号:" prop="name">
            <Input v-model="queryForm.name" placeholder="请输入"></Input>
          </FormItem>
        </Col>
        <Col span="6">
          <FormItem label="更新时间">
            <DatePicker
              type="date"
              placeholder="请选择"
              v-model="queryForm.time"
            ></DatePicker>
          </FormItem>
        </Col>

        <Col span="24">
          <Row justify="end" type="flex">
            <Col span="12" style="text-align:right">
              <FormItem class="form-btn">
                <Button type="primary" @click="search">查询</Button>
                <Button style="margin-left: 8px" @click="restore">重置</Button>
              </FormItem>
            </Col>
          </Row>
        </Col>
      </Row> -->
    </Form>
    <Row>
      <Col span="24" class-name="layout-table-tools" ref="toolsBar">
        <Row type="flex" justify="space-between">
          <Col span="6"
            ><Button type="primary" style="margin-right: 5px">
              <router-link tag="span" to="/artilce/add"
                ><Icon type="md-add-circle" /> 添加内容</router-link
              >
            </Button></Col
          >
          <Col span="12">
            <Row type="flex" justify="end">
              <Select
                style="width:120px"
                class="marginLeft"
                placeholder="选择栏目"
              >
                <Option
                  v-for="item in moveList"
                  :value="item.value"
                  :key="item.value"
                  >{{ item.label }}</Option
                > </Select
              ><Input
                suffix="ios-search"
                placeholder="搜索"
                style="width: auto"
                class="marginLeft"
              /> </Row
          ></Col>
        </Row>
      </Col>
      <Col span="24" class-name="layout-table-list">
        <Alert show-icon>
          已选择 <span>{{ selectList.length }}</span> 项 总内容量
          <span>{{ count }}</span> 条
        </Alert>
        <Table
          :size="tableOpts.size"
          :height="tableOpts.height"
          @on-selection-change="selectChange"
          :loading="loading"
          ref="selection"
          :columns="userTableCol"
          :data="userTableData"
          tooltip
        >
        </Table>
        <Row type="flex" style="margin-top:5px">
          <Col span="24">
            <Button type="primary" @click="save">保存</Button>
            <Button @click="batchdetele" class="marginLeft">删除</Button>
            <Select
              v-model="model1"
              style="width:120px"
              class="marginLeft"
              placeholder="状态修改"
            >
              <Option
                v-for="item in stateList"
                :value="item.value"
                :key="item.value"
                >{{ item.label }}</Option
              >
            </Select>
            <Select
              v-model="model1"
              style="width:120px"
              placeholder="移动"
              class="marginLeft"
            >
              <Option value="移动到指定栏目" disabled>移动到指定栏目</Option>
              <Option
                v-for="item in moveList"
                :value="item.value"
                :key="item.value"
                >{{ item.label }}</Option
              >
            </Select></Col
          >
        </Row>
      </Col>
      <Col
        span="24"
        class-name="layout-table-pages"
        v-if="userTableData.length > 0"
      >
        <Page
          show-total
          @on-change="changepageindex"
          :total="count"
          :current="pageIndex"
          :page-size="pageSize"
        />
      </Col>
    </Row>
  </Card>
</template>
<script>
import { appVersionList, appVersionDelete } from '@/api/application.js'
import { SERVER_CODE } from '@/utils/constants.js'
import { spinShowMixins } from '@/mixins/spin-mixins.js'
import { formatDateT } from '@/utils/common.js'
export default {
  components: {},
  mixins: [spinShowMixins],
  data() {
    return {
      count: 0,
      pageIndex: 1,
      pageSize: 10,
      loading: false, //表格数据加载
      stateList: [
        {
          value: '首页推荐',
          label: '首页推荐'
        },
        {
          value: '取消首页推荐',
          label: '取消首页推荐'
        },
        {
          value: '置顶',
          label: '置顶'
        },
        {
          value: '取消置顶',
          label: '取消置顶'
        }
      ],
      moveList: [
        {
          value: '服务中心',
          label: '服务中心'
        },
        {
          value: '口岸资讯',
          label: '口岸资讯'
        }
      ],
      queryForm: {
        id: '',
        code: '',
        name: '',
        time: ''
      },
      selectList: [],
      userTableCol: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '标题',
          key: 'id'
        },
        {
          title: '所属栏目',
          key: 'code'
        },
        {
          title: '访问量',
          key: 'intro',
          width: 200
        },
        {
          title: '首页推荐',
          key: 'info',
          width: 200
        },
        {
          title: '置顶',
          key: 'name'
        },
        {
          title: '更新时间',
          key: 'updateTime',
          render: (h, params) => {
            return h('div', formatDateT(params.row.updateTime))
          }
        },
        {
          title: '操作',
          key: 'action',
          width: 200,
          fixed: 'right',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h(
                'Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.index)
                    }
                  }
                },
                '编辑'
              ),
              h(
                'Button',
                {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                },
                '删除'
              ),
              h(
                'Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      //this.remove(params.index)
                    }
                  }
                },
                '预览'
              )
            ])
          }
        }
      ],
      userTableData: []
    }
  },
  created() {
    if (this.$store.state.applicationStatus) {
      this.getData(1) //页面是否重新加载数据
    }
  },
  activated() {
    this.getData(1) //如果应用修改加载数据
  },
  methods: {
    getData(index) {
      if (index && index == 1) {
        //处理点击分页然后搜索页数不归1问题
        this.pageIndex = 1
      }
      this.loading = true
      let params = {
        // id: this.queryForm.id,
        // code: this.queryForm.code,
        // page: this.pageIndex,
        // size: this.pageSize
      }
      appVersionList(params)
        .then(({ code, data, msg }) => {
          if (code === SERVER_CODE.CODE_200) {
            this.userTableData = data.content
            this.count = data.count
            this.selectList = [] //清空单选框选择数据
          } else {
            this.$Message.info(msg)
          }
        })
        .catch(error => {
          console.log(error)
        })
        .finally(() => {
          this.loading = false
        })
    },
    search() {
      //搜索
      this.getData(1)
    },
    restore() {
      //重置搜索框
      this.queryForm = {
        id: '',
        code: '',
        name: '',
        time: ''
      }
    },
    changepageindex(index) {
      //获取分页index
      this.pageIndex = index
      this.getData()
    },
    batchdetele() {
      //批量删除
      if (this.selectList.length == 0) {
        this.$Message.info('请选择删除对象！')
      } else {
        this.userTableData = []
        this.selectList = [] //清空单选框选择数据
      }
    },
    save() {
      //批量保存
      if (this.selectList.length == 0) {
        this.$Message.info('请选择保存对象！')
      } else {
        this.$Message.success('保存成功！')
        this.$refs.selection.selectAll(false)
      }
    },
    selectChange(res) {
      //选择框状态变化监听
      this.selectList = res
    },
    edit(index) {
      //跳转编辑页面
      // this.$store.commit('setapplicationValue', this.userTableData[index])
      sessionStorage.setItem(
        'applicationValue',
        JSON.stringify(this.userTableData[index])
      )
      this.$router.push('/article/edit')
    },
    remove(index) {
      //删除一条数据
      this.$Modal.confirm({
        title: '确认删除',
        content: '<p>该操作将导致数据无法恢复，请谨慎操作！</p>',
        onOk: () => {
          let params = { id: this.userTableData[index].id }
          appVersionDelete(params)
            .then(({ code, msg }) => {
              if (code === SERVER_CODE.CODE_200) {
                this.$Message.success('删除成功！')
                this.userTableData.splice(index, 1)
                this.selectList = [] //清空单选框选择数据
                this.count--
              } else {
                this.$Message.info(msg)
                console.log('应用删除失败')
              }
            })
            .catch(error => {
              console.log(error)
            })
            .finally(() => {})
        },
        onCancel: () => {}
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.layout-table-tools {
  button {
    margin-right: 12px;
  }
}
.marginLeft {
  margin-left: 5px;
}
</style>
